@use "../utils/functions/" as *;
@use "../utils/mixins/" as *;

.portfolio {
  $root: &;

  &__inner {
    @include flex-sb;

    align-items: flex-end;
    margin-bottom: 50px;
    gap: 15px;
  }

  &__slider {
    overflow: hidden;
    margin-bottom: 50px;
  }

  &__nav {
    @include flex;

    gap: 30px;
  }

  &__item {
    @include flex-sb;

    gap: 20px;

    @include media(mobile-l) {
      flex-direction: column;
    }
  }

  &__image {
    width: 100%;
    max-width: 930px;

    img {
      overflow: hidden;
      border-radius: calc(var(--radius-big) + 2px);
      height: 100%;
    }
  }

  &__content {
    align-self: center;
    width: 100%;
    max-width: 430px;

    h3 {
      margin-bottom: 30px;
      font-weight: var(--fw-900);
      font-size: clamp(20px, 0.8333rem + 1.8519vi, 40px);
      line-height: 1.3;
    }
  }

  &__btns {
    @include flex-all-sb;

    gap: 15px;

    .btn {
      max-width: 204px;
    }
  }

  &__details {
    @include flex-col;

    margin-bottom: 30px;
    gap: 20px;
  }

  &__detail {
    @include flex-v-center;

    position: relative;
    white-space: nowrap;
    padding-left: 25px;
    font-weight: var(--fw-700);
    font-size: clamp(16px, 0.9571rem + 0.1905vi, 18px);

    &::before {
      content: "";
      position: absolute;
      left: 0;
      border-radius: var(--radius-round);
      width: 10px;
      height: 10px;
      background-color: currentcolor;
    }

    dt {
      @include flex-v-center;

      position: relative;
      flex: 0 1 100%;
      overflow: hidden;
      white-space: nowrap;

      &::after {
        @include mask;

        content: "";
        position: relative;
        display: block;
        margin: 0 18px;
        width: 100%;
        height: 6px;
        mask-size: contain;
        mask-image: url("../images/icons/line.svg");
        background-color: currentColor;
      }
    }
  }

  &__category {
    display: block;
    margin-bottom: 50px;
    font-size: 15px;
    line-height: 1.5;
    color: var(--color-gray-100);
  }

  &--main {
    #{$root}__list {
      @include flex-col;
      gap: 30px;
    }

    h2 {
      margin-bottom: 50px;
    }
  }

  .btn:not(.btn--transparent) {
    max-width: 280px;
  }

  .custom-checker {
    margin-bottom: 50px;
  }

  .section-heading {
    margin-bottom: 0;
  }

  @include media(tablet-wide) {
    &__category {
      margin-bottom: 25px;
    }
  }

  @include media(tablet) {
    &__inner {
      margin-bottom: 30px;
    }

    &__item {
      flex-direction: column;
    }

    &__nav {
      gap: 15px;
    }

    &__content {
      max-width: 100%;
    }

    &--main {
      h2 {
        margin-bottom: 25px;
      }
    }
  }

  @include media(mobile-m) {
    .btn.btn {
      max-width: 100%;
    }
  }

  @include media(mobile-s) {
    &__inner,
    &__btns {
      flex-direction: column;
      gap: 25px;
    }
  }
}
