@use "../utils/functions/" as *;
@use "../utils/mixins/" as *;

.nav {
  @include flex-all-sb;

  position: relative;

  &__inner {
    @include flex-v-center;

    width: 100%;
  }

  &__list {
    @include flex-all-center;

    margin: 0 auto;

    li {
      &:not(:last-child) {
        margin-right: 50px;
      }

      .sub-menu {
        position: absolute;
        top: 100%;
        z-index: 2;
        display: none;
        border: 1px solid var(--color-orange-400);
        padding: 15px;
        width: fit-content;
        height: auto;
        background-color: var(--color-dark-900);
        opacity: 0;
        visibility: hidden;
        transition: opacity 0.4s linear;

        li {
          &:not(:last-child) {
            margin-bottom: 10px;
          }
        }

        a {
          &::before {
            bottom: -5px;
          }
        }

        &.show {
          display: block;
          opacity: 1;
          visibility: visible;
        }
      }
    }

    a {
      position: relative;
      font-size: 18px;
      transition: color 0.5s linear;

      &::before {
        content: "";
        position: absolute;
        left: 0;
        bottom: -12px;
        width: 100%;
        height: 2px;
        background-color: currentcolor;
        transform: scale(0);
        transform-origin: left;
        transition: transform 0.3s linear;
      }

      &:hover,
      &.true {
        color: var(--color-orange-400);

        &::before {
          transform: scale(1);
        }
      }
    }

    .link-dropdown {
      @include flex-all-center;

      gap: 10px;

      &::after {
        @include mask;

        mask-image: url("../images/icons/chevron.svg");
        width: 10px;
        height: 6px;
      }
    }
  }

  @include media(tablet-wide) {
    li {
      &:not(:last-child) {
        margin-right: 20px;
      }
    }
  }

  @include media(tablet) {
    &__inner {
      position: absolute;
      right: calc(var(--container-offset) * -1);
      top: calc(var(--header-height) / 1.6);
      z-index: -1;
      flex-direction: column;
      justify-content: flex-start;
      overflow-y: auto;
      padding: 40px 0;
      width: 100%;
      height: calc(var(--vh) - var(--header-height));
      max-width: 350px;
      background-color: var(--color-dark-900);
      transform: translateX(150%);
      transition-property: transform;
      transition-duration: 0.25s;
    }

    &__list {
      z-index: -1;
      flex-direction: column;
      margin: 0;
    }

    li {
      &:not(:last-child) {
        margin-right: 0;
      }

      &:not(.sub-menu > li,
      .social__item) {
        margin-bottom: 30px;
      }
    }

    &__link {
      font-size: 20px;
    }

    .sub-menu {
      &.show {
        position: relative;
        top: 0;
        left: 0;
      }
    }
  }
}
