@use "../utils/functions" as *;
@use "../utils/mixins" as *;

.faq {
  $root: &;

  &__list {
    @include flex-col;

    gap: 20px;

    &--home {
      #{$root}__item {
        @include flex-v-center;
      }
    }
  }

  &__item {
    margin-left: auto;
    border-radius: 20px 20px 0;
    padding: 30px 30px 30px 75px;
    background-color: var(--color-brown-400);
    position: relative;
    width: 100%;
    max-width: 930px;
    gap: 30px;

    span {
      font-weight: var(--fw-700);
      font-size: clamp(20px, 0.875rem + 1.1111vw, 30px);
      line-height: 1.5;
    }

    p {
      letter-spacing: 0.04em;
    }

    a:not([class]) {
      text-decoration: underline;
      transition: color 0.3s linear;

      &:hover {
        color: var(--color-orange-400);
      }
    }

    &.revealed {
      p {
        --bgColor: var(--color-brown-400);
        --lines: 500;
        --lineHeight: 1.5rem;
        --timePerLine: 3s;
        --widthCh: 100%;
        --width: calc(var(--widthCh) * 1ch);
        /* do not touch the time property!!! */
        --time: calc(var(--lines) * var(--timePerLine));

        background: var(--bgColor);
        line-height: var(--lineHeight);
        max-height: var(--lineHeight);
        overflow: hidden;
        position: relative;
        width: var(--width);
        animation: grow var(--time) steps(var(--lines));
        animation-fill-mode: forwards;

        &::before {
          content: "";
          position: absolute;
          border-left: 2px solid;
          bottom: 0;
          height: 2em;
          right: 0;
          width: 100%;
          animation:
            type var(--timePerLine) linear infinite,
            carriageReturn var(--time) steps(var(--lines)) var(--lines),
            caret 0.5s steps(2) infinite;
          background: var(--bgColor);
        }
      }
    }

    &--manager {
      margin: 0 auto 0 0;
      border-radius: 20px 20px 20px 0;
      padding: 30px 60px;
      color: var(--color-dark-900);
      background-color: var(--color-white);

      &.revealed p {
        --bgColor: var(--color-white);
      }
    }
  }

  &__link {
    @include flex-v-center;
    gap: 30px;

    &::before {
      content: "";
      position: absolute;
      inset: 0;
    }
  }

  &__panels {
    animation:
      slide-up-slow 2s forwards,
      fade-in 2s forwards;

    div {
      @include flex-v-center;
      gap: 30px;
    }
  }

  @include media(tablet) {
    &__item {
      padding: 16px;
      gap: 10px;
    }
  }

  @include media(mobile-m) {
    &__link,
    #{$root}__item,
    &__panels > div {
      flex-direction: column;
      align-items: flex-start;
      gap: 10px;
    }
  }
}
