@use "../utils/functions/" as *;
@use "../utils/mixins" as *;

.seo {
  &__inner {
    @include flex-sb;
    gap: 25px;
  }

  &__desc {
    @include flex-col;

    position: relative;
    align-self: center;
    padding-top: 5.98rem;
    width: 100%;
    height: 100%;
    max-width: 920px;

    &::before {
      @include mask;

      content: "";
      position: absolute;
      left: 0;
      top: 33px;
      mask-image: url("../images/icons/quotes.svg");
      width: 32px;
      height: 22px;
      background-color: var(--color-brown-400);
    }

    p {
      margin-bottom: 30px;
      font-weight: var(--fw-700);
      font-size: clamp(20px, 0.875rem + 1.1111vi, 30px);
      line-height: 1.5;
      letter-spacing: 0.03em;
    }

    cite {
      font-size: 18px;
    }
  }

  &__image {
    img {
      overflow: hidden;
      border-radius: var(--radius-big);
      height: 100%;
    }
  }

  @include media(mobile-l) {
    &__inner {
      flex-direction: column;
      align-items: center;
    }
  }
}
