@use "../utils/functions/" as *;
@use "../utils/mixins/" as *;

.header {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  z-index: 10;
  margin: 0 auto;
  padding: 40px 0;
  width: 100%;
  background-color: var(--color-dark-900);
  transition-property:  transform, padding;
  transition-duration: 0.15s;

  &.hide {
    transform: translateY(calc(-100% - 30px));
  }

  // &.scroll {
  //   padding: 20px 0;
  // }

  @include media(tablet) {
    padding: 20px 0;
  }

  .social {
    display: none;
  }

  &--simple {
    .nav {
      @include media(mobile-m) {
        flex-direction: column;
        gap: 25px;
      }
    }

    .social {
      display: flex;
    }

    .nav__inner,
    .burger {
      display: none;
    }
  }
}
