/* stylelint-disable color-function-notation */
@import url("https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100..900;1,100..900&display=swap");

:root {
  --scheme: dark;
  --content-width: 1410px;
  --container-offset: 16px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // font-family
  --font-family: "Raleway", sans-serif;

  // font-weight
  --fw-500: 500;
  --fw-700: 700;
  --fw-900: 900;

  // color
  --color-dark-900: #001322;
  --color-blue-400: #283642;
  --color-orange-400: #f15a25;
  --color-brown-400: #907f53;
  --color-white: #fff;
  --color-gray-10: #bababa;
  --color-gray-100: #7c7c7c;
  --color-red-600: #aa192a;
  color-scheme: dark;

  //  radius
  --radius-main: 5px;
  --radius-big: 15px;
  --radius-round: 50%;

  &[data-theme="light"] {
    color-scheme: light;
    --color-white: #283642;
    --color-dark-900: #fff;

    .btn--transparent {
      &::before {
        background-color: var(--color-orange-400);
      }

      &:hover,
      &:focus {
        color: #fff;
      }
    }

    .reviews__image {
      filter: brightness(0) saturate(100%) invert(16%) sepia(14%) saturate(1277%) hue-rotate(166deg) brightness(93%)
        contrast(86%);
    }

    .social__link {
      background-color: #bababa;
      &::before {
        background-color: #fff;
      }

      &:hover {
        background-color: var(--color-orange-400);
      }
    }

    .stages__item {
      color: #fff;

      &::after {
        background-color: #fff;
      }

      &::before {
        -webkit-text-stroke-color: #283642;
      }
    }

    .service-card {
      & h3 {
        &::before {
          filter: brightness(0) saturate(100%) invert(19%) sepia(6%) saturate(2367%) hue-rotate(167deg) brightness(92%)
            contrast(91%);
        }
      }

      &:hover:not(.services--article article) {
        color: #fff;

        a::before,
        h3::before {
          filter: brightness(0) saturate(100%) invert(100%) sepia(100%) saturate(0%) hue-rotate(342deg) brightness(103%)
            contrast(101%);
        }
      }
    }

    .services--article article {
      color: #fff;
    }

    .footer {
      &__copyright,
      a {
        color: #fff;
      }

      span {
        &::before {
          background-color: #fff;
        }
      }
    }
  }

  // shadow

  // gradient

  //filter
  // --filter-dark-900: brightness(0) saturate(100%) invert(8%) sepia(9%) saturate(4899%) hue-rotate(158deg) brightness(95%) contrast(103%);
  --filter-dark-900: invert(8%) sepia(9%) saturate(4899%) hue-rotate(166deg) brightness(95%) contrast(103%);

  --filter-coral-400: invert(46%) sepia(76%) saturate(3551%) hue-rotate(349deg) brightness(99%) contrast(91%);

  --filter-white: invert(100%) sepia(0%) saturate(0%) hue-rotate(244deg) brightness(104%) contrast(103%);

  --filter-gray-100: invert(48%) sepia(0%) saturate(7478%) hue-rotate(151deg) brightness(104%) contrast(85%);

