@use "../utils/functions" as *;
@use "../utils/mixins" as *;

.stages {
  $root: &;

  &__list {
    counter-reset: stages;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    margin-bottom: 50px;
    padding-left: clamp(35px, 4.16vw, 60px);
    gap: 45px;

    &--advantages {
      padding: 0 clamp(35px, 4.16vw, 60px) 0 0;

      #{$root}__item {
        padding-bottom: 20px;

        &::after {
          left: auto;
          right: 0;
          background-size: 70px;
          transform: translate(66%, -50%);
        }

        p {
          margin-bottom: 0;
          font-weight: var(--fw-700);
          font-size: clamp(16px, 0.9571rem + 0.1905vi, 18px);
          line-height: 1.65;
        }

        &:last-of-type {
          &::after {
            right: 4%;
          }
        }
      }
    }

    &--services {
      margin: 0;
      padding: 0 clamp(35px, 4.16vw, 60px) 0 0;
      gap: 5.72rem 45px;

      #{$root}__item {
        padding-bottom: 44px;

        h3 {
          margin-bottom: 20px;
          font-size: clamp(20px, 1.1458rem + 0.463vw, 25px);
          line-height: 1.3;
          letter-spacing: 0.06em;
        }

        &:nth-child(-n + 3),
        &:nth-child(n + 7) {
          translate: clamp(35px, 4.16vw, 60px);
        }

        &:nth-child(n + 4) {
          &::after {
            left: auto;
            right: 0;
            transform: translate(65%, -50%);
          }
        }

        &:nth-child(n + 7) {
          &::after {
            left: 0;
            transform: translate(-65%, -50%);
          }
        }

        &:nth-child(7) {
          &::after {
            left: 4%;
          }
        }

        &:last-child,
        &:nth-child(6) {
          &::after {
            right: 4%;
          }
        }
      }
    }
  }

  &__item {
    @include flex-col;

    counter-increment: stages;
    position: relative;
    border-radius: var(--radius-big);
    padding: 70px clamp(30px, 5.4vw, 80px) 30px clamp(30px, 5.4vw, 80px);
    height: 100%;
    line-height: 1.5;
    background-color: var(--color-brown-400);

    &::before {
      content: counter(stages, decimal-leading-zero);
      position: absolute;
      display: block;
      left: 60px;
      top: 0;
      font-weight: var(--fw-700);
      font-size: clamp(60px, 2.9167rem + 3.7037vi, 100px);
      line-height: 1.17;
      letter-spacing: 0.03em;
      background-image: linear-gradient(180deg, #fff 50%, rgb(144, 127, 83) 50%);
      transform: translateY(-50%);
      -webkit-text-stroke-width: 0.006em;
      -webkit-text-stroke-color: inherit;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }

    &::after {
      @include flex-all-center;
      @include cover-background;

      content: "";
      position: absolute;
      left: 0;
      top: 50%;
      z-index: 3;
      border: clamp(2px, 0.55vw, 8px) solid var(--color-brown-400);
      border-radius: var(--radius-round);
      width: 120px;
      height: 120px;
      background-image: url("../images/icons/design.svg");
      background-size: 50px;
      background-color: var(--color-white);
      transform: translate(-65%, -50%);
    }

    h3 {
      margin-bottom: 20px;
      font-weight: var(--fw-700);
      font-size: clamp(20px, 0.875rem + 1.1111vi, 30px);
      line-height: 1.5;
      letter-spacing: 0.03em;
    }

    p,
    span {
      font-size: 15px;
      letter-spacing: 0.02em;
    }

    p {
      margin-bottom: 22px;
      min-height: 92px;
    }

    span {
      margin-top: auto;
    }

    &--design {
      &::after {
        background-image: url("../images/icons/design.svg");
      }
    }

    &--coding {
      &::after {
        background-image: url("../images/icons/coding.svg");
      }
    }

    &--testing {
      &::after {
        background-image: url("../images/icons/testing.svg");
      }
    }

    &--sol {
      &::after {
        background-image: url("../images/icons/money.svg");
      }
    }

    &--save {
      &::after {
        background-image: url("../images/icons/hand-money.svg");
      }
    }

    &--discount {
      &::after {
        background-image: url("../images/icons/making-money.svg");
      }
    }

    &--brief {
      &::after {
        background-image: url("../images/icons/brief.svg");
      }
    }

    &--fraymes {
      &::after {
        background-image: url("../images/icons/framers.svg");
      }
    }

    &--back {
      &::after {
        background-image: url("../images/icons/php.svg");
      }
    }

    &--project {
      &::after {
        background-image: url("../images/icons/project.svg");
      }
    }

    &--adv {
      &::after {
        background-image: url("../images/icons/adv.svg");
      }
    }

    &--smm {
      &::after {
        background-image: url("../images/icons/smm.svg");
      }
    }

    &:first-of-type:not(.stages__list--advantages .stages__item) {
      &::after {
        left: 4%;
      }
    }
  }

  .btn {
    max-width: 314px;
  }

  .section-heading {
    margin-bottom: 5.83rem;
  }

  @include media(desktop) {
    &__item {
      padding: 40px 30px 30px 40px;
    }

    &--advantages {
      #{$root}__item {
        padding-bottom: 30px;
      }
    }
  }

  @include media(tablet-wide) {
    &__item {
      &::after {
        width: 80px;
        height: 80px;
        background-size: 30px;
      }
    }

    &__list {
      &--services {
        gap: 45px;
      }
    }

    &--advantages {
      #{$root}__item {
        &::after {
          background-size: 40px;
        }
      }
    }
  }

  @include media(tablet) {
    &--digital {
      #{$root}__list {
        #{$root}__item {
          &:last-child {
            grid-column: 2 span;
          }
        }
      }
    }

    &__list {
      grid-template-columns: repeat(2, 1fr);

      &--services {
        padding: 0 clamp(35px, 4.16vw, 60px);

        #{$root}__item {
          &:nth-child(n + 4) {
            padding: 30px 45px 16px 16px;

            &::after {
              right: 4%;
            }
          }

          &:nth-child(-n + 3),
          &:nth-child(n + 7) {
            translate: none;
            padding: 30px 16px 16px 45px;
            &::after {
              right: 0;
              left: 0;
              transform: translate(-50%, -50%);
            }
          }

          &:last-child {
            grid-column: auto;
          }
        }
      }

      &--advantages {
        #{$root}__item {
          &::after {
            transform: translate(50%, -50%);
          }

          &:last-of-type {
            &::after {
              right: 0;
            }
          }
        }
      }
    }

    &__item {
      padding: 30px 16px 16px 45px;

      &::after {
        transform: translate(-50%, -50%);
      }

      &:first-of-type:not(.stages--advantages .stages__item) {
        &::after {
          left: 0;
        }
      }

      &:last-child {
        grid-column: 2 span;
      }

      p {
        min-height: max-content;
      }
    }

    h3 {
      margin-bottom: 15px;
    }

    .section-heading {
      margin-bottom: 50px;
    }

    &--advantages {
      #{$root}__item {
        padding: 35px 40px;
      }
    }
  }

  @include media(mobile-m) {
    &--digital {
      #{$root}__list {
        #{$root}__item {
          &:last-child {
            grid-column: auto;
          }
        }
      }
    }
    &__list {
      grid-template-columns: 1fr;
      margin-bottom: 30px;
    }

    &__item {
      &:last-child {
        grid-column: auto;
      }
    }

    .btn {
      max-width: 100%;
    }
  }
}
