@use '../utils/functions/' as *;
@use '../utils/mixins' as *;

.social {
  @include flex;

  gap: 30px;

  &__link {
    @include flex-all-center;

    position: relative;
    border-radius: var(--radius-round);
    width: 40px;
    height: 40px;
    background-color: var(--color-blue-400);
    transition: background-color 0.4s linear;

    &::before {
      @include mask;

      mask-image: url('../images/icons/fb.svg');
      width: 20px;
      height: 20px;
      background-color: var(--color-gray-100);
    }

    &:hover {
      background-color: var(--color-white);

      &::before {
        background-color: var(--color-orange-400);
      }
    }

    &--fb {
      &::before {
        mask-image: url('../images/icons/fb.svg');
      }
    }

    &--ln {
      &::before {
        mask-image: url('../images/icons/ln.svg');
      }
    }

    &--insta {
      &::before {
        mask-image: url("../images/icons/insta.svg");
    }
  }

    &--yt {
      &::before {
        mask-image: url('../images/icons/yt.svg');
      }
    }

    &--be {
      &::before {
        mask-image: url('../images/icons/be.svg');
      }
    }
  }
}
