/* stylelint-disable declaration-no-important */
@use "../utils/functions/"as *;
@use "../utils/mixins"as *;

.custom-checker {
  position: relative;
  @include flex-all-sb;
  border: 1px solid var(--color-gray-100);
  border-radius: calc(var(--radius-big) * 5);
  width: fit-content;
  height: fit-content;

  @include media(mobile-s) {
    width: 320px;
    min-height: 55px;
    border-radius: 0;
    border-color: transparent;
  }

  &__item {
    display: flex;
    height: 100%;
    border-radius: calc(var(--radius-big) * 5);

    @include media(mobile-s) {
      height: fit-content;
      padding-bottom: 15px;
      border-radius: calc(var(--radius-big));
    }
  }

  .ps__rail-x,
  .ps__rail-x:hover,
  .ps__rail-x:focus,
  .ps__rail-x.ps--clicking{
    background-color: var(--color-dark-900);
    opacity: .9;
  }

  .ps__thumb-x,
  .ps__thumb-x:hover,
  .ps__thumb-x:focus,
  .ps__rail-x.ps--clicking .ps__thumb-x {
    background-color: var(--color-orange-400);
    opacity: .9;
  }

  &__label {
    overflow: hidden;
    height: 100%;
    border-radius: calc(var(--radius-big) * 5);
    display: inline-block;
    color: var(--color-gray-100);
    white-space: nowrap;
    padding: 20px 30px;
    cursor: pointer;
    font-size: clamp(16px, 1vw, 18px);
    width: 100%;
    text-align: center;
    transition-property: background-color, color;
    transition-duration: .5s;
    transition-timing-function: linear;

    @include media(tablet) {
      padding: 10px 15px;
    }
  }

  &__input {
    position: absolute;
    opacity: 0;
    pointer-events: none;
  }

  &__input:checked~.custom-checker__label {
    background-color: var(--color-orange-400);
    color: var(--color-white);
  }

  &__input:hover~.custom-checker__label:not(.custom-checker__input:checked~.custom-checker__label) {
    color: var(--color-white);

    @include media(tablet) {
      color: var(--color-gray-100);
    }
  }
}