@use "../utils/functions/" as *;

.team {
  $root: &;

  &__list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 330px));
    gap: 50px 30px;
    justify-content: center;

    @include media(tablet-wide) {
      gap: 24px;
    }

    @include media(mobile-l) {
      gap: 16px;
    }
  }

  &__member {
    padding: 30px;
    cursor: pointer;
    border: 1px solid var(--color-dark-900);
    border-radius: var(--radius-big);
    transition: border-color 0.3s;

    @include media(tablet-wide) {
      padding: 16px;
    }

    @include media(mobile-l) {
      padding: 8px;
    }

    &:hover {
      border-color: var(--color-orange-400);
    }
  }

  &__img {
    max-width: 100%;
    width: 100%;
    object-fit: cover;
    border-radius: var(--radius-big);
    margin-bottom: 20px;

    @include media(mobile-l) {
      max-height: 380px;
    }
  }

  &__person-info {
    display: flex;
    gap: 15px;
    align-items: center;
    margin-bottom: 20px;

    @include media(mobile-l) {
      flex-direction: column;
      gap: 4px;
      align-items: flex-start;
    }

    .team__name,
    .team__position {
      font-size: 15px;
      font-weight: var(--fw-500);
      letter-spacing: 0.03em;
    }
  }

  &__name {
    color: var(--color-white);
  }

  &__position {
    color: var(--color-gray-10);
  }

  &--reduced {
    @include media(desktop-wide) {
      #{$root}__list {
        grid-template-columns: repeat(4, 1fr);
        gap: 30px;
      }

      #{$root}__member {
        padding: 20px;
      }
    }

    @include media(desktop) {
      #{$root}__list {
        grid-template-columns: repeat(2, 1fr);
        #{$root}__member {
          padding: 10px;
        }
      }
    }

    @include media(mobile-m) {
      #{$root}__list {
        grid-template-columns: 1fr;

        img {
          aspect-ratio: 16/9;
        }
      }
    }
  }

  .social {
    flex-direction: row;
    gap: 15px;

    &__link {
      z-index: 3;
      width: 30px;
      height: 30px;
      background-color: var(--color-white);

      &::before {
        width: 15px;
        height: 15px;
      }
    }
  }
}
