/* stylelint-disable declaration-no-important */
@use "../utils/functions/"as *;
@use "../utils/mixins"as *;

.service-card {
  border: 1px solid var(--color-white);
  border-radius: var(--radius-big);
  height: 100%;
  transition-property:
  transform, opacity, filter, color, background-color;
  transition-duration: 0.3s;
  transition-timing-function: linear;

  &:hover {
    color: var(--color-orange-400);
    background-color: var(--color-white);

    @include media(tablet) {
      color: inherit;
      background-color: transparent;
    }

    a::before,
    h3::before {
      filter: var(--filter-coral-400);

      @include media(tablet) {
        filter: var(--filter-white);
      }
    }
  }

  &--digital {
    h3 {
      &::before {
        background-image: var(--digital);
      }
    }
  }

  &--seo {
    h3 {
      &::before {
        background-image: var(--seo);
      }
    }
  }

  &--smm {
    h3 {
      &::before {
        background-image: var(--smm);
      }
    }
  }

  &--advertising {
    h3 {
      &::before {
        background-image: var(--advertising);
      }
    }
  }

  &--landing {
    h3 {
      &::before {
        background-image: var(--landing);
      }
    }
  }

  &--multi-page {
    h3 {
      &::before {
        background-image: var(--multi-page);
      }
    }
  }

  &--support {
    h3 {
      &::before {
        background-image: var(--support);
      }
    }
  }

  &--redesign {
    h3 {
      &::before {
        background-image: var(--redesign);
      }
    }
  }

  &--logo {
    h3 {
      &::before {
        background-image: var(--logo-dev);
      }
    }
  }

  a {
    position: relative;
    display: flex;
    flex-direction: column;
    height: 100%;
    padding: 25px;

    &::before {
      position: absolute;
      content: "";
      background-image: var(--cursor);
      filter: var(--filter-white);
      @include contain-background;
      bottom: 30px;
      right: 30px;
      width: 32px;
      height: 32px;
      transition:
        filter 0.1s linear;
    }
  }

  p {
    margin-bottom: 20px;

    @include media(tablet) {
      font-size: 16px;
    }
  }

  h3 {
    position: relative;
    @include flex-v-center;
    min-height: 105px;
    margin-bottom: 10px;
    padding-left: rem(80);

    @include media(tablet) {
      padding-left: rem(60);
      min-height: 70px;
    }

    &::before {
      position: absolute;
      content: "";
      filter: var(--filter-white);
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      @include contain-background;
      width: 70px;
      height: 70px;
      transition:
        filter 0.1s ease;

      @include media(tablet) {
        width: 50px;
        height: 50px;
      }
    }
  }

  &__price {
    margin-top: auto;
    @include flex-v-center;
    gap: 50px;

    @include media(tablet) {
      gap: 15px;
    }
  }
}