@use "../utils/functions/" as *;
@use "../utils/mixins" as *;

html,
body {
  scroll-behavior: smooth;
  overscroll-behavior-y: none;
}

@media (prefers-color-scheme: dark) {
}

.page {
  height: var(--vh);
  height: 100dvh;
  font-family: var(--font-family);
  font-weight: var(--fw-500);
  font-style: normal;
  font-variant-numeric: lining-nums proportional-nums;
  font-size: clamp(16px, 1vw, 20px);
  line-height: 1.65;
  letter-spacing: 0.03em;
  color: var(--color-white);
  background-color: var(--color-dark-900);
  font-optical-sizing: auto;
  color-scheme: dark light;

  &__body {
    position: relative;
    min-width: 320px;
    min-height: var(--vh);

    &::before {
      content: "";
      position: absolute;
      z-index: -1;
      background-color: rgba(39, 40, 43, 0.75);
      opacity: 0;
      transition: opacity 0.3s;
      inset: 0;
    }
  }

  &.open {
    overflow: hidden;

    > .page__body::before {
      z-index: 5;
      opacity: 1;
      height: calc(var(--vh, 1vh) * 100);
    }
  }
}

.main {
  padding-top: var(--header-height);

  &--top {
    padding-top: 150px;

    @include media(tablet) {
      padding-top: 100px;
    }
  }

  &--empty {
    padding-top: 45px;
  }
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  width: 100%;
  max-width: var(--container-width);
}

.site-container {
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 1fr auto;
  overflow-x: hidden;
  min-height: var(--vh);
}

.logo {
  position: relative;
  display: block;
  width: 100%;
  max-width: 181px;

  img {
    width: 100%;
  }

  svg {
    fill: #fff;
  }
}

.canvas {
  position: absolute;
  top: 0;
  z-index: -1;
  inset: 0;
}

.section {
  margin-bottom: rem(125);

  @include media(tablet) {
    margin-bottom: 75px;
  }

  @include media(mobile-m) {
    margin-bottom: 35px;
  }
}

.link {
  display: inline-flex;
  align-items: center;
  font-size: clamp(16px, calc(0.975rem + 0.125vw), 18px);
  white-space: nowrap;
  text-decoration: underline;
  text-decoration-color: transparent;
  color: var(--color-orange-400);
  transition: text-decoration-color 0.3s linear;
  gap: 15px;

  &::after,
  &::before {
    @include mask;

    mask-image: url("../images/icons/arrow.svg");
    width: 16px;
    height: 16px;
    transition: transform 0.5s linear;
  }

  &::before {
    content: none;
  }

  &:hover {
    &::after,
    &::before {
      transform: translateX(5px);
    }
  }

  &--view {
    &::before {
      content: "";
      left: auto;
      mask-image: url("../images/icons/pdf.svg");
      width: 24px;
      height: 24px;
      transition: transform 0.5s linear;
    }

    &::after {
      content: none;
    }
  }
}

.wrapper {
  @include flex-col;
  align-items: start;
  gap: 50px;

  @include media(tablet) {
    gap: rem(30);
  }
}
