@use "../../utils/functions" as *;
@use "../../utils/mixins" as *;

.blog {
  article {
    border-radius: var(--radius-big);
    overflow: hidden;
    max-width: 330px;
    width: 100%;
    margin: 0 auto;

    @include media (mobile-m) {
      max-width: 280px;
    }
  }

  a:not(.btn) {
    @include flex-col;
    gap: rem(45);
    padding: rem(40);
    transition-property: background-color, color;
    transition-duration: .3s;
    transition-timing-function: linear;

    @include media (tablet) {
      padding: rem(20);
    }

    header>span {
      color: var(--color-gray-100);

      @include media (tablet) {
        color: inherit;
      }
    }

    &:hover {
      background-color: var(--color-white);
      color: var(--color-dark-900);

      @include media (tablet) {
        background-color: inherit;
        color: inherit;
      }

      .link::before {
        width: 55px;

        @include media (tablet) {
          width: 30px;
        }
      }

      div>span::before {
        background-color: var(--color-dark-900);

        @include media (tablet) {
          background-color: var(--color-white);
        }
      }
    }

    header>div {
      @include flex;
      gap: 30px;
    }

    div>span {
      position: relative;
      padding-left: 35px;

      &::before {
        content: "";
        position: absolute;
        @include cover-background;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        width: 25px;
        height: 25px;
        background-color: var(--color-white);
        transition-property: background-color;
        transition-duration: .3s;
        transition-timing-function: ease-out;
      }

      &:first-child {
        &::before {
          mask-image: url("../images/icons/icon-calendar.svg");
        }
      }

      &:last-child {
        &::before {
          mask-image: url("../images/icons/icon-calendar.svg");
        }
      }
    }

  }

  .section-heading {
    margin-bottom: 0;
  }

  span {
    display: inline-block;
    white-space: nowrap;
  }

  span:not(.link) {
    font-size: 15px;
  }

  header>span {
    margin-bottom: 10px;
    transition-property: color;
    transition-duration: .3s;
    transition-timing-function: linear;
  }

  h3 {
    font-size: clamp(20px, calc(1rem + 1.25vw), 30px);
    line-height: 1.4;
    font-weight: var(--fw-700);
  }

  .link {
    position: relative;
    justify-content: space-between;

    &::after,
    &::before {
      top: 50%;
      transform: translateY(-50%);
    }

    &::after {
      position: absolute;
      right: 0;
    }

    &::before {
      content: "";
      position: absolute;
      width: 30px;
      height: 1px;
      transform: translateY(-49%);
      right: 6px;
      border-top: 3px solid var(--color-orange-400);
      mask-image: none;
      background-color: transparent;
      transition-property: width;
      transition-duration: .3s;
      transition-timing-function: linear;
    }
  }

  &__slider {
    position: relative;
    max-width: 1410px;
    width: 100%;
    overflow: visible;
  }

  &__item {
    opacity: 1;
    transition-property: opacity;
    transition-duration: .3s;
    transition-timing-function: linear;

    &--faded {
      opacity: 0.5;
      pointer-events: none;
    }
  }

  &__slider-button {
    position: absolute;
    z-index: 1;
    font-size: 0;
    cursor: pointer;
    top: 50%;
    transform: translateY(-50%);
    width: 70px;
    height: 70px;
    background-color: var(--color-white);
    border-radius: var(--radius-round);

    @include media(tablet) {
      opacity: .6;
    }

    @include media(mobile-l) {
      width: 50px;
      height: 50px;
    }

    @include media (mobile-m) {
      width: 40px;
      height: 40px;
    }

    &::after {
      font-size: 0;
    }

    &::before {
      position: absolute;
      @include contain-background;
      top: 50%;
      transform: translateY(-50%);
      content: "";
      background-image: var(--chevron);
      width: 35px;
      height: 35px;
      filter:var(--filter-coral-400);

      @include media(mobile-l) {
        width: 25px;
        height: 25px;
      }

      @include media (mobile-m) {
        width: 20px;
        height: 20px;
      }
    }

    &.swiper-button-disabled {
      display: none;
    }

    &--prev {
      left: 0;

      &::before {
        right: 50%;
        transform: translateX(45%) translateY(-50%) rotate(90deg);
      }
    }

    &--next {
      right: 0;

      &::before {
        left: 50%;
        transform: translateX(-45%) translateY(-50%) rotate(-90deg);
      }
    }
  }

  &--about {
    .section-heading {
      width: 100%;
      max-width: 1170px;
    }
  }
}
