/* stylelint-disable declaration-no-important */
@use "../utils/functions/"as *;
@use "../utils/mixins"as *;

.services {
  &__list {
    display: grid;
    grid-template-columns: repeat(3, minmax(350px, 450px));
    justify-content: center;
    align-self: center;
    gap: 30px;
    margin-bottom: 50px;

    @include media(tablet-wide) {
      grid-template-columns: repeat(2, 1fr);
      gap: 20px;
    }

    @include media(mobile-m) {
      grid-template-columns: 1fr;
    }
  }
  &__list~p {
    max-width: 690px;
    width: 100%;
    margin-bottom: 50px;
  }

  &__item {
    opacity: 1;
    transform: scale(1);

    &--hide {
    opacity: 0;
    transform: scale(0.1);
    pointer-events: none;
    transition-property: opacity, transform;
    transition-duration: .3s;
    transition-timing-function: linear;
    }

    &--hidden {
      display: none;
    }
  }

  .btn {
    max-width: 315px;
    padding-block: 20px;
  }

  span:last-child {
    font-size: 40px;
    letter-spacing: 0.2rem;
    line-height: 1.45;
    font-weight: var(--fw-900);

    @include media(tablet) {
      font-size: 32px;
    }

    @include media(mobile-l) {
      font-size: 28px;
    }
  }

  &__inner {
    @include flex-sb;
    width: 100%;
    gap: rem(50);

    @include media(mobile-l) {
      gap: rem(20);
      flex-direction: column;
    }
  }

  .link {
    &::before {
      @include mask;
      mask-image: url("../images/icons/arrow.svg");
      transform: rotate(180deg);
      transition-property: transform;
      transition-duration: .5s;
      transition-timing-function: linear;
    }

    &::after {
      content: none;
    }

    &:hover {

      &::before {
        transform:rotate(180deg)  translateX(5px);

        @include media(tablet) {
          transform:rotate(180deg)  translateX(0);
        }
      }
    }
  }

  .section-heading {
    margin-bottom: 0;
  }

  &--filter {
    .title {
      margin-bottom: 0;
    }
  }

  &--article {
    span:last-child {
      font-size: clamp(30px, 0.9107rem + 4.2857vi, 75px);
      font-weight: var(--fw-700);
    }

    h2 {
      margin-bottom: 0;
    }

    .title {
      @include media(tablet) {
        line-height: 1.2;
      }
    }

    .services__list {
      grid-template-columns: repeat(2, 1fr);
      gap: 20px 30px;
      margin-bottom: 0;

      @include media(mobile-m) {
        grid-template-columns: 1fr;
      }
    }

    article {
      padding: 50px;
      color: var(--color-orange-400);
      background-color: var(--color-white);

      @include media(tablet) {
        padding: 30px;
      }
    }

    article>h3 {
      padding-left: 0;
      margin-bottom: 20px;
      min-height: fit-content;
    }

    article>p {
      margin-bottom: 0;
    }
  }
}