@use "../../utils/functions" as *;
@use "../../utils/mixins" as *;

.hero {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: calc(var(--header-height)) 0 55px 0;
  height: calc(var(--vh) - var(--header-height));

  &__inner {
    @include flex-sb;

    position: relative;
    z-index: 1;
    align-items: baseline;
    gap: 25px;
  }

  &__content {
    margin-right: auto;
    width: 100%;
    max-width: 904px;
  }

  .btn {
    max-width: 314px;
  }

  h1 {
    margin-bottom: 5.2rem;
  }

  .lang {
    translate: 0 -18px;
  }

  .social {
    flex-direction: column;
    align-self: flex-start;
    margin-right: 4.16rem;
  }

  @include media(tablet) {
    height: calc(var(--vh) - 50px);
    min-height: 50vh;
    min-height: 50dvh;

    h1 {
      margin-bottom: 50px;
    }

    .social {
      margin-right: 15px;
    }
  }

  @include media(mobile-l) {
    &__inner {
      flex-direction: column-reverse;
      justify-content: center;
    }

    .social {
      flex-direction: row;
      margin: 0 auto;
    }

    .lang {
      flex-direction: row;
      translate: none;
    }
  }

  @include media(mobile-m) {
    .btn {
      width: 100%;
      max-width: 100%;
    }

    h1 {
      font-size: 45px;
    }
  }
}
