@use "../utils/functions/" as *;
@use "../utils/mixins/" as *;

.footer {
  padding: 7.81rem 0;
  background-color: var(--color-brown-400);

  &__inner {
    @include flex-all-sb;

    flex-wrap: wrap;
    gap: 20px;
  }

  &__copyright {
    @include flex-all-sb;

    font-size: 18px;
    letter-spacing: 0.04em;
    gap: 30px;

    span {
      position: relative;

      &::before {
        content: "";
        position: absolute;
        right: -15px;
        width: 1px;
        height: 100%;
        background-color: var(--color-white);
      }
    }
  }

  &__link {
    text-decoration: underline;
    transition: color 0.5s linear;
    text-underline-offset: 1px;

    &:hover {
      color: var(--color-orange-400) !important;
    }
  }

  .logo {
    max-width: 210px;
  }

  .btn {
    max-width: 260px;
  }

  @include media(tablet-wide) {
    padding: 80px 0;
  }

  @include media(tablet) {
    padding: 40px 0;
  }

  @include media(mobile-l) {
    &__inner {
      flex-direction: column;
      justify-content: center;
    }
  }

  @include media(mobile-m) {
    padding: 30px 0;
  }
}
