@use "../utils/functions/" as *;
@use "../utils/mixins" as *;

.lang {
  @include flex-v-center;

  flex-direction: column;
  gap: 15px;

  a {
    font-size: 18px;
    text-transform: uppercase;
    color: var(--color-gray-100);
    transition: color 0.5s linear;

    &:hover,
    &:focus {
      color: var(--color-orange-400);
    }
  }

  .active {
    font-weight: var(--fw-900);
    color: var(--color-orange-400);
  }
}
