@use "../utils/functions/" as *;
@use "../utils/mixins/" as *;

h1,
h2,
h3 {
  overflow: hidden;
  width: fit-content;
  letter-spacing: 0.03em;
}

h1 {
  font-weight: var(--fw-900);
  font-size: clamp(35px, 0.6408rem + 7.2785vw, 150px);
  line-height: 1.17;
  letter-spacing: 0.04em;
}

h2,
.title {
  margin-bottom: 20px;
  font-weight: var(--fw-700);
  font-size: clamp(30px, 0.9107rem + 4.2857vi, 75px);
  line-height: 1.5;
}

.card-title,
h3 {
  font-weight: var(--fw-900);
  font-size: clamp(20px, calc(1rem + 1.25vw), 40px);
  line-height: 1.3;
  letter-spacing: 0.06em;
}

p {
  font-size: clamp(16px, 0.9571rem + 0.1905vi, 18px);
}
