@use "../utils/functions/" as *;
@use "../utils/mixins/" as *;

.theme-switch {
  position: relative;
  display: flex;
  align-items: center;
  overflow: hidden;
  border-radius: 100px;
  width: 100%;
  max-width: 90px;
  min-height: 50px;
  background-color: var(--color-blue-400);
  transition: background-color 0.6s;

  &__icon {
    display: none;
    position: absolute;

    &.light {
      left: 5px;
    }
  }

  &__icon--active {
    left: auto;
    right: 5px;
    display: flex;
  }
}
